export const serverUrl =
    process.env.NODE_ENV === 'production' ?
        'https://www.kaixa.cn' :
        'http://127.0.0.1:5000';

export const urlFor = (path: string) => {
    let apiUrl: string;
    if (path.startsWith('/')) {
        apiUrl = serverUrl + path;
    }
    apiUrl = serverUrl + '/' + path;
    if (apiUrl.endsWith('/')) {
        return apiUrl;
    }
    return apiUrl + '/';
};

export const webUrl =
    process.env.NODE_ENV === 'production' ?
        'http://localhost:3000/' :
        'https://woodykaixa.github.io/';